.container-autocomplete-results {
  border-radius: 20px;
  border: 1px solid #572ee5;
  width: 580px;
  margin-top: 10px;
}
.search-autocomplete {
  display: flex;
  cursor: pointer;
}
.search-autocomplete img {
  width: 22px;
}
.search-autocomplete li {
  list-style: none;
  margin: 0 0 10px 18px;
  font-size: 18px;
}
